'use strict';

// Получить элемент изображения паука
const spiderImg = document.querySelector('.spider');

// Получить ширину и высоту изображения паука
const spiderWidth = spiderImg.offsetWidth;
const spiderHeight = spiderImg.offsetHeight;

// Получить элемент поля
const field = document.querySelector('.wall');

// Получить ширину и высоту элемента поля
const fieldWidth = field.clientWidth;
const fieldHeight = field.clientHeight;

// Рассчитать позиции слева и сверху, чтобы центрировать
// изображение паука внутри элемента поля
const x = (fieldWidth - spiderWidth) / 2;
const y = (fieldHeight - spiderHeight) / 2;

// // Установить позицию элемента изображения паука
spiderImg.style.left = x + 'px';
spiderImg.style.top = y + 'px';


